<template>
  <div id="login_container">
    <!-- <div class="leaves">
      <div class="set">
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
        <div><img src="../assets/imgs/login_leaf.png" /></div>
      </div>
    </div> -->
    <!-- <img src="../assets/imgs/login_bgcolor.jpg" class="bg" /> -->
    <div class="login_box">
      <div class="login_form">
        <h2 id="title" class="ccxkt">LOGIN</h2>
        <div class="inputs">
          <div class="input_box">
            <!-- required就是不能为空 必须在css效果中有很大的作用 -->
            <!-- 可以简写为required -->
            <input type="text" v-model="loginForm.username" required /><label
              >username</label
            >
          </div>
          <div class="input_box">
            <input
              type="password"
              v-model="loginForm.password"
              required="required"
            /><label>password</label>
          </div>
        </div>
        <!--按钮区-->
        <div class="btns">
          <button id="login" @click.prevent="login">
            登录
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </button>
          <button id="reset" @click.prevent="resetLoginForm">
            重置
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {login as login_api } from "@/api";
export default {
  data() {
    return {
      loginForm: {
        username: "admin",
        password: "123456",
      },
      initForm: null,
      lock:false,
    };
  },
  methods: {
    resetLoginForm() {
      this.loginForm = this.$deepCopy(this.initForm);
    },
    async login() {
      if(this.lock){
        return;
      }
      this.lock = true;
      if (
        this.loginForm.username.length <= 0 ||
        this.loginForm.password.length <= 0
      ) {
        return this.$message.error("账号或密码不能为空");
      }
      try {
      console.log(1);

        const res = await login_api(this.loginForm);
        if (res.code === 0) {
          return this.$message.error("账号或密码错误");
          
        }
        this.$message.success("登录成功");
        window.sessionStorage.setItem("id", res.data.id);
        this.$router.push("home");
      } catch (err) {
        this.$message.error("网络请求失败");
      }
      this.lock = false;
    },
  },
  mounted() {
    this.initForm = this.$deepCopy(this.loginForm);
  },
};
</script>
<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#login_container,
.login_box::before {
  background: url("../assets/imgs/login_bgcolor.jpg") 0 / cover fixed;
}
#login_container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .login_box {
    position: relative;
    background: hsla(0, 0%, 100%, 0.5);
    overflow: hidden;
    width: max-content;
    height: max-content;
    border: 1px solid transparent;
    background-clip: padding-box;
    border-right-color: rgba(255, 255, 255, 0.3);
    border-bottom-color: rgba(255, 255, 255, 0.3);
    border-radius: 20px;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      filter: blur(20px);
      margin: -30px;
    }
    .login_form {
      position: relative;
      padding: 50px;
      width: 480px;
      display: flex;
      flex-direction: column;
      height: 400px;
      justify-content: space-between;
      align-items: center;
      #title {
        color: var(--primary1);
        font-size: 30px;
      }
      .inputs {
        width: 75%;
      }
      .input_box {
        /* 相对定位 */
        position: relative;
        width: 100%;

        input,
        label {
          font-size: 14px !important;
        }
        input {
          /* 清除input框自带的边框和轮廓 */
          outline: none;
          border: none;
          width: 100%;
          padding: 10px 0;
          margin-bottom: 30px;
          color: #fff;
          font-size: 16px;
          border-bottom: 1px solid #fff;
          /* 背景颜色为透明色 */
          background-color: transparent;
        }
        label {
          position: absolute;
          top: 0;
          left: 0;
          padding: 10px 0;
          color: grey;
          /* 这个属性的默认值是auto 默认是这个元素可以被点击 但是如果我们写了none 就是这个元素不能被点击 , 就好像它可见但是不能用 可望而不可即 */
          /* 这个就是两者的区别 */
          pointer-events: none;
          /* 加个过渡 */
          transition: all 0.5s;
        }
        input:focus + label,
        input:valid + label {
          top: -20px;
          color: var(--primary1);
          font-size: 12px;
        }
      }
      #login {
        margin-right: 50px;
      }
      button {
        overflow: hidden;
        position: relative;
        padding: 10px 20px;
        background: none;
        border: none;
        color: var(--primary1);
        /* 取消a表现原有的下划线 */
        text-decoration: none;
        /* 同样加个过渡 */
        transition: all 0.5s;
        &:hover {
          color: #fff;
          border-radius: 5px;
          background-color: var(--primary1);
          box-shadow: 0 0 5px var(--primary1), 0 0 5px var(--primary1),
            0 0 50px var(--primary1), 0 0 20px var(--primary1);
        }
        &:focus{
          transform: translateY(2px);
        }
        span {
          position: absolute;
          &:first-child {
            top: 0;
            left: -100%;
            width: 100%;
            height: 2px;
            /* to right 就是往右边 下面的同理 */
            background: linear-gradient(to right, transparent, var(--primary1));
            /* 动画 名称 时长 linear是匀速运动 infinite是无限次运动 */
            animation: move1 1s linear infinite;
          }
          &:nth-child(2) {
            right: 0;
            top: -100%;
            width: 2px;
            height: 100%;
            background: linear-gradient(transparent, #03e9f4);
            /* 这里多了个0.25s其实是延迟时间 */
            animation: move2 1s linear 0.25s infinite;
          }
          &:nth-child(3) {
            right: -100%;
            bottom: 0;
            width: 100%;
            height: 2px;
            background: linear-gradient(to left, transparent, #03e9f4);
            animation: move3 1s linear 0.5s infinite;
          }
          &:last-child {
            left: 0;
            bottom: -100%;
            width: 2px;
            height: 100%;
            background: linear-gradient(#03e9f4, transparent);
            animation: move4 1s linear 0.75s infinite;
          }
          @keyframes move1 {
            0% {
              left: -100%;
            }
            50%,
            100% {
              left: 100%;
            }
          }
          @keyframes move2 {
            0% {
              top: -100%;
            }
            50%,
            100% {
              top: 100%;
            }
          }
          @keyframes move3 {
            0% {
              right: -100%;
            }
            50%,
            100% {
              right: 100%;
            }
          }
          @keyframes move4 {
            0% {
              bottom: -100%;
            }
            50%,
            100% {
              bottom: 100%;
            }
          }
        }
      }
    }
  }
}
@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes caret {
  50% {
    border-color: transparent;
  }
}

